<template>
    <div>
        <Header class="header">
        <h2 align="center">软件测试工具</h2>
        </Header>
        <Row :gutter=12>
            <i-col span="6" >
                <Card title="文件上传" align="center">
                    <UploadFile msg="上传Jar文件" :fmt="srcFmt" :url="jarUploadUrl"/>
                    <br/>
                    <Divider/>
                    <br/>
                    <UploadFile msg="上传测试数据文件" :fmt="dataFmt" :url="dataUploadUrl"/>
                </Card>
            </i-col>
            <i-col span="18">
                <Card title="运行测试" align="center">
                    <Row :gutter=12>
                        <i-col span="6">
                            <MethodSelector placeholder="请选择Jar文件" :reqinfo="jarListReqInfo" type="jar" @updateSelect="updateJarName"/>
                        </i-col>
                        <i-col span="6">
                            <MethodSelector placeholder="请选择类" :reqinfo="classListReqInfo" type="cls" @updateSelect="updateClsName"/>
                        </i-col>
                        <i-col span="6">
                            <MethodSelector placeholder="请选择方法" :reqinfo="methodListReqInfo" type="method" @updateSelect="updateMtdSig"/>
                        </i-col>
                        <i-col span="6">
                            <MethodSelector placeholder="请选择测试数据文件" :reqinfo="dataListReqInfo" @updateSelect="updateExcelName"/>
                        </i-col>
                    </Row>
                    <br/>
                    <Button type="primary" long :loading=running @click="runTest">{{testButton}}</Button>
                </Card>
                <br/>
                <Card title="结果展示" align="center">
                    <Row :gutter=10>
                        <i-col span="12">
                            <Input type="textarea" placeholder="测试结果" rows=10 v-model="testlog"/>
                        </i-col>

                        <i-col span="12">
                            <ResultChart v-if="showChart" :percentage="percentage" :passed="passed"/>
                        </i-col>
                    </Row>
                </Card>
            </i-col>
        </Row>
    </div>
</template>

<script>
    import UploadFile from "@/components/UploadFile";
    import MethodSelector from '@/components/MethodSelector'
    import ResultChart from '@/components/ResultChart'
    import axios from 'axios'

    export default {
        name: 'App',
        data (){
            return{
                running: false,
                showChart: false,
                testButton: '开始测试',
                testlog: '',
                percentage: 0,
                passed: 0,
                srcFmt: ['jar', 'java'],
                dataFmt: ['xls', 'xlsx'],
                jarUploadUrl:'/upload/jar',
                dataUploadUrl:'/upload/excel',
                jarName: '',
                className: '',
                methodSignature: '',
                excelName: '',
                jarListReqInfo: {
                    url: '/list/jars',
                    params: {}
                },
                dataListReqInfo: {
                    url: '/list/excels',
                    params: {}
                }
            }
        },
        computed: {
            classListReqInfo: function() {
                return {
                    url: '/jar/classes',
                    params: {
                        jarName: this.jarName
                    }
                }
            },
            methodListReqInfo: function() {
                return {
                    url: '/jar/methods',
                    params: {
                        jarName: this.jarName,
                        className: this.className
                    }
                }
            }
        },
        methods:{
            runTest: function(){
                this.running = true;
                this.testButton  = "正在测试中"
                this.showChart = false;
                axios.post('/test/run', {
                    excelFileName: this.excelName,
                    methodLocator: {
                        className: this.className,
                        jarName: this.jarName,
                        methodSignature: this.methodSignature
                    }
                }).then(res => {
                    let data = res.data;
                    this.running = false;
                    this.testButton = "开始测试",
                    this.percentage = data['percentage'];
                    this.passed = data['passNum'];
                    this.showChart = true;
                    this.testlog = data['failLog'];
                    console.log(data);
                }).catch(error => {
                    console.log(error);
                    this.testButton = "开始测试";
                    this.running = false;
                    this.$Notice.error({
                        title: "Test process failed!"
                    });
                });
            },
            updateJarName: function(value){
                console.log("Parent receive jar " + value);
                this.jarName = value;
            },
            updateClsName: function (value) {
                console.log("Parent receive cls " + value)
                this.className = value;
            },
            updateMtdSig: function (value) {
                console.log("Parent receive mtd " + value);
                this.methodSignature = value;
            },
            updateExcelName: function (value) {
                console.log("parent receive excel " + value);
                this.excelName = value;
            }
        },
        components: {
            UploadFile,
            MethodSelector,
            ResultChart
        }
    }
</script>

<style scoped>
    .header{
        background-color: #effffc;
    }
</style>